body {
  margin: 0;
}

button {
  cursor: pointer;
}

img {
  width: 100%;
  height: 180px;
  display: block;
}

.search-form {
  background-color: rgba(132, 37, 223, .76);
  justify-content: center;
  align-items: center;
  gap: 10px;
  margin-bottom: 25px;
  padding-top: 15px;
  padding-bottom: 15px;
  display: flex;
}

.search-form input {
  width: 300px;
  height: 23px;
  border: none;
  border-radius: 5px;
  outline-color: #50f5ff;
  padding-left: 10px;
}

.form-btn {
  min-width: 70px;
  min-height: 25px;
  background-color: #50f5ff;
  border: 1px solid rgba(0, 0, 0, 0);
  border-radius: 5px;
  font-size: 14px;
  font-weight: 500;
  display: block;
}

.form-btn:hover, .form-btn:focus {
  background-color: #50ccff;
  border: 1px solid #50ccff;
}

.container {
  width: 1170px;
  margin-left: auto;
  margin-right: auto;
  padding-left: 15px;
  padding-right: 15px;
}

.gallery {
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  gap: 15px;
  margin-bottom: 20px;
  display: flex;
}

.photo-card {
  flex-basis: calc(25% - 11.25px);
  box-shadow: 0 1px 2px rgba(60, 64, 67, .3), 0 2px 6px 2px rgba(60, 64, 67, .15);
}

.info {
  justify-content: center;
  align-items: center;
  gap: 15px;
  padding: 10px;
  font-size: 14px;
  display: flex;
}

.info-item {
  flex-direction: column;
  align-items: center;
  gap: 5px;
  margin: 0;
  display: flex;
}

.load-more-btn {
  min-height: 35px;
  background-color: #0c25e3;
  border: none;
  border-radius: 3px;
  justify-content: center;
  align-items: center;
  gap: 5px;
  margin-left: auto;
  margin-right: auto;
  display: flex;
}

.loader {
  width: 25px;
  height: 25px;
  box-sizing: border-box;
  border: 5px dotted #fff;
  border-radius: 50%;
  animation: rotation 2s linear infinite;
  display: inline-block;
  position: relative;
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

.btn-label {
  color: #fff;
  font-size: 16px;
